.works-main {
  padding: 25px 0 50px 0;
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 25px;
    p {
      font-size: 16px;
      line-height: 166%;
      color: #535353;
    }
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 96px / 4));
    gap: 32px;
    border-top: 1px solid #EDEDED;
    padding-top: 25px;
    margin-top: 25px;
  }
  &__btn {
    padding: 12px 56px;
    margin: 50px auto 0;
  }
}
.works-menu {
  &__slider {
    background: #fff;
    box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.05);
    padding: 18px 0;
  }
  .swiper {
    padding-bottom: 18px;
    overflow: unset;
  }
  .tab {
    position: relative;
    width: auto !important;
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    color: #535353;
    cursor: pointer;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -4px;
      width: 100%;
      height: 1px;
      background: #0C0537;
      opacity: 0;
      pointer-events: none;
    }
  }
  .active-tab {
    font-weight: 600;
    &:after {
      opacity: 1;
      pointer-events: auto;
    }
  }
}
.works-main__card {
  &-img {
    position: relative;
    display: block;
    padding-bottom: 60%;
    overflow: hidden;
    &:hover {
      img {
        transform: scale(1.25);
      }
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: all 0.7s;
    }
  }
  &-title {
    display: block;
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
    color: #0C0537;
    margin-top: 16px;
  }
  &-text {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 16px;
    p {
      font-size: 16px;
      line-height: 166%;
      color: #535353;
    }
  }
}
.works-info {
  color: #535353;
  padding: 50px 0 100px;
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    p {
      font-size: 16px;
      line-height: 166%;
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      li {
        font-size: 16px;
        line-height: 166%;
      }
    }
  }
}
.works-main__card-big {
  margin-top: 25px;
  .works-main__card-title {
    font-weight: 600;
    margin: 0 0 16px 0;
    @media screen and (min-width: 993px) {
      font-size: 32px;
    }
  }
  .works-main__card-text {
    @media screen and (min-width: 993px) {
      p {
        font-size: 20px;
      }
    }
  }
}
.products {
  border-top: 1px solid #EDEDED;
  padding: 25px 0 50px;
  h6 {
    color: #535353;
  }
  &__slider {
    position: relative;
    padding: 0 40px 32px;
    margin-top: 24px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-button-prev {
    top: calc(50% - 32px);
    left: 0;
  }
  .swiper-button-next {
    top: calc(50% - 32px);
    right: 0;
  }
}
.product-card {
  position: relative;
  height: auto;
  display: flex;
  flex-direction: column;
  background: #fff;
  border: 1px solid #EAEAEA;
  padding: 24px 12px;
  &__info {
    position: absolute;
    top: 0;
    left: 0;
    background: #0C0537;
    padding: 4px 6px;
    span {
      font-size: 16px;
      line-height: 105%;
      font-weight: 600;
      color: #fff;
    }
  }
  &__img {
    display: flex;
    justify-content: center;
    margin-bottom: auto;
    img {
      width: 100%;
      max-width: 100%;
    }
  }
  &__title {
    font-size: 16px;
    line-height: 120%;
    color: #2A2A2A;
    margin-top: 32px;
  }
}
.products2 {
  padding: 50px 0 100px;
  &__slider {
    position: relative;
    padding: 0 40px 32px;
    margin-top: 24px;
  }
  &__btn {
    padding: 12px 56px;
    margin: 50px auto 0;
  }
  .swiper-button-prev {
    top: calc(50% - 32px);
    left: 0;
  }
  .swiper-button-next {
    top: calc(50% - 32px);
    right: 0;
  }
}
.product2-card {
  &__img {
    position: relative;
    display: block;
    padding-bottom: 60%;
    overflow: hidden;
    &:hover {
      img {
        transform: scale(1.25);
      }
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: all 0.7s;
    }
  }
  &__title {
    display: block;
    font-size: 16px;
    line-height: 120%;
    margin-top: 16px;
  }
}


@media screen and (max-width: 1200px) {
  .works-main {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 64px / 3));
    }
  }
}

@media screen and (max-width: 992px) {
  .works-main {
    padding: 18px 0 25px 0;
  }
  .works-info {
    padding: 25px 0 50px;
  }
  .products {
    padding-bottom: 25px;
    margin-top: 25px;
    &__slider {
      padding-left: 0;
      padding-right: 0;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  .products2 {
    padding: 25px 0 50px;
    &__slider {
      padding-left: 0;
      padding-right: 0;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .works-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
    }
  }
  .works-main__card {
    &-title {
      font-size: 16px;
    }
  }
  .products {
    .swiper {
      overflow: unset;
    }
  }
  .products2 {
    .swiper {
      overflow: unset;
    }
  }
}

@media screen and (max-width: 576px) {
  .works-main {
    &__text {
      margin-top: 20px;
      p {
        font-size: 12px;
      }
    }
  }
  .works-menu {
    .tab {
      font-size: 12px;
    }
  }
  .works-main__card {
    &-title {
      font-size: 14px;
    }
  }
  .works-info {
    &__text {
      p {
        font-size: 12px;
      }
      ul {
        li {
          font-size: 12px;
        }
      }
    }
  }
  .works-main__card-big {
    margin-top: 20px;
  }
  .products {
    &__slider {
      margin-top: 12px;
    }
  }
  .product-card {
    &__info {
      span {
        font-size: 12px;
      }
    }
    &__title {
      font-size: 12px;
    }
  }
  .products2 {
    &__slider {
      margin-top: 12px;
    }
  }
  .product2-card {
    &__title {
      font-size: 12px;
      margin-top: 12px;
    }
  }
}

@media screen and (max-width: 400px) {
  .works-main {
    &__cards {
      grid-template-columns: 100%;
      gap: 32px;
    }
  }
  .works-main__card {
    &:nth-child(7) ~ .works-main__card {
      display: none;
    }
    &-title {
      font-size: 16px;
    }
  }
}