.breadcrumbs {
  background: #fff;
  padding: 18px 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px 24px;
  }
  &__item {
    position: relative;
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    color: #7C7C7C;
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -15px;
        transform: translate(0, -50%);
        width: 4px;
        height: 1px;
        background: #7C7C7C;
        pointer-events: none;
      }
    }
  }
}
h2 {
  font-size: 40px;
  line-height: 120%;
  font-weight: 800;
  color: #535353;
}
h3 {
  font-size: 30px;
  line-height: 166%;
  font-weight: 600;
}
.btn-blue {
  background: #0C0537;
  &:hover {
    background: #1c0c78;
  }
  span {
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
    color: #fff;
  }
}
.swiper-button-prev,
.swiper-button-next {
  &:after {
    color: #000;
  }
}
.swiper-pagination {
  top: unset !important;
  bottom: 0;
  background: transparent;
  &-progressbar-fill {
    background: #BDBDBD !important;
  }
}

@media screen and (max-width: 992px) {
  h2 {
    font-size: 30px;
  }
  h3 {
    font-size: 24px;
  }
}

@media screen and (max-width: 576px) {
  .breadcrumbs {
    &__item {
      font-size: 12px;
    }
  }
  h2 {
    font-size: 18px;
  }
  h3 {
    font-size: 14px;
  }
  .btn-blue {
    span {
      font-size: 14px;
    }
  }
}