.footer {
  background: #3D4F64;
  color: #fff;
  padding: 50px 0;
  &__logo {
    width: 120px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__items {
   display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 32px;
    margin-top: 50px;
  }
  &__item {
    &:last-child {
      flex-shrink: 0;
    }
  }
  &__subtitle {
    display: block;
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    &:not(:last-child) {
      margin-bottom: 15px;
    }
    a:hover {
      color: #F3A800;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    li a {
      font-size: 16px;
      line-height: 120%;
      &:hover {
        color: #F3A800;
      }
    }
  }
  &__subitems {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__subitem {
    display: flex;
    align-items: center;
    gap: 10px;
    &:hover {
      color: #F3A800;
    }
    i {
      max-width: 20px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img {
        max-width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 120%;
    }
  }
  &__text {
    text-align: center;
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
    margin-top: 50px;
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__items {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 32px / 2));
      margin-top: 32px;
    }
    &__item {
      &:last-child {
        grid-column: 1/3;
      }
    }
    &__subitems {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 16px;
    }
    &__text {
      margin-top: 32px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    padding: 25px 0;
    &__logo {
      width: 66px;
    }
    &__subtitle {
      font-size: 14px;
    }
    &__list {
      li a {
        font-size: 12px;
      }
    }
    &__subitem {
      span {
        font-size: 12px;
      }
    }
    &__text {
      font-size: 12px;
    }
  }
}