.decisions-main {
  padding: 25px 0 50px;
  &__top {
    margin-top: 50px;
    h2 {
      text-align: center;
    }
  }
  &__img {
    position: relative;
    padding-bottom: 40%;
    margin-top: 25px;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 25px;
    margin-top: 25px;
  }
  &__item {
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 25px;
      p {
        font-size: 20px;
        line-height: 166%;
        color: #535353;
      }
    }
    &-images {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 25px / 2));
      gap: 25px;
      margin-top: 25px;
    }
    &-img {
      position: relative;
      padding-bottom: 40%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
}
.categories {
  padding: 50px 0;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 25px;
  }
  &__item {
    border: 1px solid #C9C9C9;
    &:not(:first-child) {
      margin-top: -1px;
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      padding: 16px;
    }
    .accordion-show--active {
      background: #0C0537;
      color: #fff;
      .categories__item-btn {
        transform: rotate(90deg);
        svg path {
          fill: #fff;
        }
      }
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
      font-weight: 500;
    }
    &-btn {
      width: 16px;
      transition: all 0.3s;
      svg {
        width: 100%;
        height: auto;
        path {
          transition: all 0.3s;
        }
      }
    }
    &-hidden {
      display: none;
      padding: 16px;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      p {
        font-size: 16px;
        line-height: 166%;
      }
    }
  }
}
.connection {
  padding: 50px 0;
  &__inner {
    max-width: 500px;
    margin: 0 auto;
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 25px;
    &-group {
      input {
        width: 100%;
        height: 44px;
        background: transparent;
        border: none;
        border-bottom: 1px solid #EFEFEF;
        font-size: 18px;
        line-height: 120%;
        color: #000;
        &::placeholder {
          color: #535353;
        }
      }
    }
    &-btn {
      height: 60px;
      padding: 12px;
      span {
        font-size: 18px !important;
      }
    }
  }
}
.products3 {
  padding: 50px 0 100px;
  &__slider {
    position: relative;
    padding: 0 40px 44px;
    margin-top: 25px;
  }
  .swiper-button-prev {
    top: calc(50% - 44px);
    left: 0;
  }
  .swiper-button-next {
    top: calc(50% - 44px);
    right: 0;
  }
  .swiper-pagination {
    bottom: 0;
    &-bullet {
      width: 12px;
      height: 12px;
      background: transparent;
      border: 1px solid #3D4F64;
      border-radius: 0;
      opacity: 1;
      &-active {
        background: #3D4F64;
      }
    }
  }
}
.product3-card {
  &__img {
    position: relative;
    display: block;
    padding-bottom: 60%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 16px;
    p {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
    }
  }
}

@media screen and (max-width: 992px) {
  .decisions-main {
    padding: 25px 0;
    &__top {
      margin-top: 25px;
    }
    &__img {
      margin-top: 16px;
    }
    &__item {
      &-text {
        margin-top: 16px;
        p {
          font-size: 16px;
        }
      }
      &-images {
        margin-top: 16px;
      }
    }
  }
  .categories {
    padding: 25px 0;
    &__items {
      margin-top: 16px;
    }
  }
  .connection {
    padding: 25px 0;
  }
  .products3 {
    padding: 25px 0 50px;
    &__slider {
      padding-left: 0;
      padding-right: 0;
      margin-top: 16px;
    }
    .swiper-button-prev {
      display: none;
    }
    .swiper-button-next {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .decisions-main {
    &__item {
      &-images {
        grid-template-columns: 100%;
        gap: 12px;
      }
    }
  }
  .connection {
    &__inner {
      max-width: 100%;
    }
  }
  .products3 {
    .swiper {
      overflow: unset;
    }
  }
}

@media screen and (max-width: 576px) {
  .decisions-main {
    &__item {
      &-text {
        p {
          font-size: 12px;
        }
      }
    }
  }
  .categories {
    &__item {
      &-show {
        padding: 12px;
      }
      &-title {
        font-size: 12px;
      }
      &-btn {
        width: 9px;
      }
      &-hidden {
        padding: 12px;
      }
      &-text {
        gap: 12px;
        p {
          font-size: 12px;
        }
      }
    }
  }
  .connection {
    &__form {
      &-group {
        input {
          font-size: 14px;
        }
      }
      &-btn {
        height: 44px;
        span {
          font-size: 14px !important;
        }
      }
    }
  }
  .product3-card {
    &__text {
      margin-top: 12px;
      p {
        font-size: 12px;
      }
    }
  }
}